import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { createNumberMask } from "redux-form-input-masks";
import {
  getBarang,
  getKategoriService,
} from "../../../actions/datamaster_action";
import {
  ReanderFieldInline,
  ReanderSelect,
} from "../../../components/notification/notification";
const currencyMask = createNumberMask({
  prefix: "Rp. ",
  locale: "id-ID",
});

class TambahService extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    this.props.dispatch(getKategoriService());
    this.props.dispatch(getBarang());
  }
  setSparepart(e) {
    let hasil = e.split("||");
    this.props.change("harga_sparepart", hasil[1]);
    this.props.change("kode_sparepart", hasil[0]);
    this.props.change("nama_sparepart", hasil[2]);
  }
  setService(e) {
    let hasil = e.split("||");
    this.props.change("kategori_service", hasil[0]);
    this.props.change("harga_service", hasil[1]);
    this.props.change("nama_service", hasil[2]);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div>
          <div className="row">
            <div className="col-lg-6">
              <div className="col-lg-12">
                <Field
                  name="kategori_service1"
                  component={ReanderSelect}
                  options={this.props.listkategoriservice.map((list) => {
                    let data = {
                      value: `${list.id_kategori_service}||${list.harga_jasa_service}||${list.kategori_service}`,
                      name: list.kategori_service,
                    };
                    return data;
                  })}
                  onChange={(e) => this.setService(e)}
                  type="text"
                  label="Jenis Service"
                  placeholder="Masukan Jenis Service"
                />
              </div>
              <div className="col-lg-12 d-none">
                <Field
                  name="kategori_service"
                  component={ReanderFieldInline}
                  type="text"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                />
              </div>
              <div className="col-lg-12 ">
                <Field
                  name="harga_service"
                  component={ReanderFieldInline}
                  type="telp"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-12 ">
                <Field
                  name="keterangan_service"
                  component={ReanderFieldInline}
                  type="text"
                  label="Keterangan Service"
                  placeholder="Masukan Keterangan Service"
                />
              </div>
              <div className="col-lg-12 d-none ">
                <Field
                  name="nama_service"
                  component={ReanderFieldInline}
                  type="telp"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                  {...currencyMask}
                />
              </div>
            </div>
            <div className="col-lg-6">
              <div className="col-lg-12">
                <Field
                  name="sparepart"
                  component={ReanderSelect}
                  options={this.props.listBarang.map((list) => {
                    let data = {
                      value: `${list.kode_barang}||${list.harga_jual}||${list.nama_barang}`,
                      name: list.nama_barang,
                    };
                    return data;
                  })}
                  onChange={(e) => this.setSparepart(e)}
                  type="text"
                  label="Jenis Service"
                  placeholder="Masukan Jenis Service"
                />
              </div>
              <div className="col-lg-12 d-none">
                <Field
                  name="kode_sparepart"
                  component={ReanderFieldInline}
                  type="text"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                />
              </div>
              <div className="col-lg-12 d-none">
                <Field
                  name="nama_sparepart"
                  component={ReanderFieldInline}
                  type="text"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                />
              </div>
              <div className="col-lg-12">
                <Field
                  name="harga_sparepart"
                  component={ReanderFieldInline}
                  type="telp"
                  label="Harga Service"
                  placeholder="Masukan Harga Service"
                  {...currencyMask}
                />
              </div>
              <div className="col-lg-12 ">
                <Field
                  name="keterangan_sparepart"
                  component={ReanderFieldInline}
                  type="text"
                  label="Keterangan Sparepart"
                  placeholder="Masukan Keterangan Sparepart"
                />
              </div>
            </div>
            <div className="col-lg-12">
              <div className="text-right">
                <button className="btn btn-primary">
                  Submit <i className="fa fa-paper-plane ml-3"></i>
                </button>
              </div>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

TambahService = reduxForm({
  form: "TambahService",
  enableReinitialize: true,
})(TambahService);
export default connect((state) => {
  return {
    listBarang: state.datamaster.listbarang,
    listkategoriservice: state.datamaster.listkategoriservice,
  };
})(TambahService);
